.cell {
  width: 1ch;
  display: inline-block;
  font-size: 2.2vw;
}

@media only screen and (min-width: 900px) {
  .cell  {
    font-size: 20px;
  }
}

.cell:hover {
  background-color: rgba(255,255,255,0.2);
}

.Screen {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}



